
body {font-family: Arial, Helvetica, sans-serif;}
* {box-sizing: border-box}
.maindiv{
    display: flex; 
    justify-content:center;
    align-items:center;
    padding: 16px;
}

  .inputitemscashout{
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
  }

  .inputitemscashout:focus {
    background-color: #ddd;
    outline: none;
  }

  .mainform{
    width: 500px;
  }

  .submitbtncashout {
    padding: 14px 20px;
    background-color:#4CAF50;
    float: left;
    width: 50%;
    border: none;
    margin: 8px 0;
    cursor: pointer;
  }
  .submitbtncashout:hover {
    opacity:0.6;
  }

