body {font-family: Arial, Helvetica, sans-serif;}
.maindiv{
    display: flex; 
    justify-content:center;
    align-items:center;
}
.welcome{
    text-align: center;
    animation: blinker 1s linear infinite;
    color: #4CAF50;
}
.imgcontainer {
    text-align: center;
    margin: 24px 0 12px 0;
    position: relative;
  }
img.avatar {
    width: 40%;
    border-radius: 50%;
  }
  .inputitemslogin {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }

  .loginbutton {
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    cursor: pointer;
    width: 100%;
  }
  
  .loginbutton:hover {
    opacity: 0.6;
  }

  .signupbutton{
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 15px;
    margin: 8px 0;
    cursor: pointer;
    position: absolute;
    width: 10%;
    right:100 ;
    top:50;
  }
  .signupbutton:hover {
    opacity: 0.6;
  }
  
  .clearbtnlogin {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 15px;
    margin: 8px 0;
    cursor: pointer;
    width: 30%;
  }
  .clearbtnlogin:hover {
    opacity: 0.6;
  }
 
  span.password {
    float: right;
    padding-top: 16px;
  }
  
  span.password {
    float: right;
    padding-top: 16px;
  }
  
  @media screen and (max-width: 300px) {
    span.password {
      display: block;
      float: none;
    }
    .cancelbtn {
      width: 100%;
    }
  }
  @keyframes blinker {
    50% {
      opacity: 0.3;
    }
  }

  .modal{
    background-color : transparent;
    box-shadow: none;
  }

  .modalheading{
    position: absolute;
    left: 55px;
    top: 100px;
  }

  .modalinput{
    position: absolute;
    left: 100px;
    top: 125px;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
  }

  .modalsubmitbtn{
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 10px 15px;
    margin: 8px 0;
    cursor: pointer;
    width: 20%;
    position: absolute;
    left: 150px;
    top: 180px;
  }

  .modalsubmitbtn:hover {
    opacity: 0.6;
  }