
body {font-family: Arial, Helvetica, sans-serif;}
* {box-sizing: border-box}
.maindiv{
    display: flex; 
    justify-content:center;
    align-items:center;
    padding: 16px;
}

  .inputitemssignup{
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
  }

  .inputitemssignup:focus {
    background-color: #ddd;
    outline: none;
  }

  .mainform{
    width: 500px;
  }

  button:hover {
    opacity:1;
  }
  
  .cancelbtnsignup {
    padding: 14px 20px;
    background-color: #f44336;
    float: left;
    width: 50%;
    border: none;
    margin: 8px 0;
    cursor: pointer;
  }

  .cancelbtnsignup:hover {
    opacity: 0.6;
  }
  
  hr {
    border: 1px solid #f1f1f1;
    margin-bottom: 25px;
  }

 .createaccountbtn {
  background-color: #4CAF50;
  padding: 14px 20px;
  width: 50%;
  float: left;
  border:none;
  margin: 8px 0;
  cursor: pointer;
  }

  .createaccountbtn:hover {
    opacity: 0.6;
  }

  .buttonsdiv::after {
    content: "";
    clear: both;
    display: table;
  }
  
  @media screen and (max-width: 300px) {
    .cancelbtn, .signupbtn {
      width: 100%;
    }
  }